import createImageUrlBuilder from '@sanity/image-url';
import { NextSeo } from 'next-seo';
import { OpenGraph } from 'next-seo/lib/types';
import { ReactNode } from 'react';

import Footer from '~/components/organisms/footer';
import { config } from '~/data/config';
import { CMSGlobalData } from '~/types/settings';

// import styles from './styles.module.css';

type LayoutProps = {
  title?: string;
  description?: string;
  slug: string;
  ogTitle?: string;
  ogImage?: {
    _type: string;
  };
  disallowRobots?: boolean;
  globalData: CMSGlobalData;
  children?: ReactNode;
};

const imageBuilder = createImageUrlBuilder(config);

const Layout = ({
  children,
  globalData,
  title,
  slug,
  ogTitle,
  ogImage,
  description,
  disallowRobots,
}: LayoutProps) => {
  if (globalData && globalData.seo) {
    // use global OG image if page's OG image is not set
    const ogImageSource = ogImage || globalData.seo.ogImage;

    let ogImages: OpenGraph['images'] = [];
    if (ogImageSource) {
      ogImages = [
        {
          // Facebook recommended size
          url: imageBuilder
            .image(ogImageSource)
            .width(1200)
            .height(630)
            .format('jpg')
            .quality(60)
            .url() as string,
          width: 1200,
          height: 630,
          alt: title,
        },
      ];
    }

    return (
      <>
        <NextSeo
          title={title}
          titleTemplate={`${globalData.seo.title} | %s`}
          description={description || globalData.seo.description}
          canonical={
            globalData.seo.url && `${globalData.seo.url}/${slug || ''}`
          }
          openGraph={{
            images: ogImages,
            title: ogTitle || globalData.seo.ogTitle || title,
          }}
          twitter={{
            cardType: 'summary_large_image',
            site: '@Frame_io',
          }}
          noindex={disallowRobots}
        />
        {children}
        <Footer globalData={globalData} footerData={globalData.footer} />
      </>
    );
  }
  return (
    <>
      {children}
      <Footer globalData={globalData} footerData={globalData.footer} />
    </>
  );
};

export default Layout;
