import { gsap } from 'gsap';
import { CSSProperties, useCallback, useRef } from 'react';

import ModularComponent from '~/components/organisms/ModularComponent';
import {
  cn,
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

// import UIStore from '~/state/ui';
// import {
//   useIsomorphicLayoutEffect as useLayoutEffect,
//   useScrollProgress,
// } from '~/utils';
import Shadow from '../shadow/Shadow';
import styles from './styles.module.css';
import { GarageDoor } from './types';

type props = {
  group: GarageDoor;
};

const GarageDoor = ({ group }: props) => {
  const $garageDoorContainer = useRef<HTMLDivElement>(null);
  const parallaxProgressSetter = useRef<(value: number) => void>();
  const $door = useRef<HTMLDivElement>(null);

  const $revealed = useRef<HTMLDivElement>(null);
  const onProgress = useCallback((progress: number) => {
    if (
      $revealed.current &&
      group.revealed._type !== 'module.bento' &&
      parallaxProgressSetter.current
    ) {
      parallaxProgressSetter.current((1 - progress) * 80);
    }
  }, []);

  useScrollProgress($door, onProgress);

  useLayoutEffect(() => {
    if ($garageDoorContainer.current && $door.current) {
      parallaxProgressSetter.current = gsap.quickSetter(
        $revealed.current,
        '--covered-parallax-progress',
        'vh',
      ) as (value: number) => void;
    }
  }, []);

  return (
    <div ref={$garageDoorContainer} className={styles.garageDoorContainer}>
      <Shadow className={styles.shadow} ref={$door}>
        <ModularComponent
          className={styles.door}
          {...group.door}
          isGarageDoor={true}
          garageDoorRevealType={group.revealed._type}
        />
      </Shadow>
      <div
        className={styles.revealed}
        ref={$revealed}
        {...(group.revealed.background
          ? {
              style: {
                '--revealed-module-background': group.revealed.background,
              } as CSSProperties,
            }
          : {})}
      >
        <ModularComponent
          className={cn(
            styles.sticky,
            group.revealed._type !== 'module.customImageSequence' &&
              group.revealed._type !== 'module.caseStudy' &&
              styles.extraPadding,
            group.revealed._type === 'module.customImageSequence' &&
              styles.negativeMargin,
          )}
          {...group.revealed}
          garageDoorRevealParent={$revealed}
        />
      </div>
    </div>
  );
};
export default GarageDoor;
