import { gsap } from 'gsap';
import { useCallback, useRef } from 'react';

import ModularComponent from '~/components/organisms/ModularComponent';
import {
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import styles from './styles.module.css';
import { ReverseGarageDoor } from './types';

type props = {
  group: ReverseGarageDoor;
};

const ReverseGarageDoor = ({ group }: props) => {
  const $door = useRef<HTMLDivElement>(null);
  const $covered = useRef<HTMLDivElement>(null);
  const parallaxProgressSetter = useRef<(value: number) => void>();
  const opacityProgressSetter = useRef<(value: number) => void>();
  const parallaxValue =
    group.covered._type === `module.hero.parallax` ? -80 : -180;

  const onProgress = useCallback((progress: number) => {
    if (opacityProgressSetter.current) {
      const opacityNormalizedProgress = gsap.utils.clamp(
        0,
        1,
        gsap.utils.normalize(0, 0.5, progress),
      );
      opacityProgressSetter.current(1 - opacityNormalizedProgress);
    }
    // Slowly move up the covered module
    if ($covered.current && parallaxProgressSetter.current) {
      const normalizedProgress = gsap.utils.clamp(
        0,
        1,
        gsap.utils.normalize(0, 1, progress),
      );
      parallaxProgressSetter.current(normalizedProgress * parallaxValue);
    }
  }, []);

  useLayoutEffect(() => {
    parallaxProgressSetter.current = gsap.quickSetter(
      $covered.current,
      '--covered-parallax-progress',
      'vh',
    ) as (value: number) => void;
    opacityProgressSetter.current = gsap.quickSetter(
      $covered.current,
      '--covered-opacity-progress',
    ) as (value: number) => void;
  }, []);

  useScrollProgress($door, onProgress);

  return (
    <>
      <div className={styles.covered} ref={$covered}>
        <div className={styles.sticky}>
          <ModularComponent
            {...group.covered}
            isReverseGarageDoor={true}
            reverseGarageDoorRevealParent={$covered}
          />
        </div>
      </div>
      <div className={styles.door} ref={$door}>
        <ModularComponent {...group.door} />
      </div>
    </>
  );
};
export default ReverseGarageDoor;
