import {
  ModuleComponentTypes,
  ModuleProps,
} from '~/components/organisms/types';

import moduleConfig from './config';

const ModularComponent = (module: ModuleProps) => {
  const Module: ModuleComponentTypes | null =
    moduleConfig[module._type]?.Module ?? null;

  if (!Module) {
    console.warn(
      `ModularComponent (~/components/organisms/ModularComponent.tsx): Module _key '${module._key}' _ref '${module._ref}' has not been correctly mapped. Please check ~/components/organisms/config.ts.`,
    );
    return null;
  }

  return <Module {...module} />;
};

export default ModularComponent;
