import { gsap } from 'gsap';
import Link from 'next/link';
import { useCallback, useRef } from 'react';

import AdobeLogo from '~/components/atoms/adobeLogo';
import { BaseCMSButtonProps } from '~/components/atoms/Buttons/Ctas/ButtonBase.types.d';
import { CMSLinkProps } from '~/components/atoms/link/types';
import Logo from '~/components/molecules/logo';
import { LogoLinkProps } from '~/components/molecules/logoLink/types';
import SvgOrImage from '~/components/molecules/svgOrImage';
import {
  useIsomorphicLayoutEffect as useLayoutEffect,
  useScrollProgress,
} from '~/utils';

import { CMSNavGroupLink } from '../navigation/navGroup/types';
import FooterGroup from './footerGroup';
import FooterItem from './footerItem';
import styles from './styles.module.css';
import { CMSFooterProps } from './types';

const Footer = ({ footerData }: CMSFooterProps) => {
  const $footer = useRef<HTMLDivElement>(null);
  const progressSetter = useRef<(value: string) => void>();

  const onProgress = useCallback((progress: number) => {
    if (progressSetter.current) progressSetter.current(`${progress * -10}rem`);
  }, []);

  useScrollProgress($footer, onProgress);

  useLayoutEffect(() => {
    progressSetter.current = gsap.quickSetter(
      $footer.current,
      '--footer-progress',
    ) as (value: string) => void;
  }, []);

  if (footerData) {
    return (
      <footer ref={$footer} className={styles.footer}>
        <div className={styles.rowPrimary}>
          <div className={styles.columnLogo}>
            <Link href={footerData.primaryLogoLeft?.link?.url || '/'}>
              <a>
                {footerData.primaryLogoLeft.logo && (
                  <Logo
                    className={styles.primaryLogo}
                    name={footerData.primaryLogoLeft.logo.name}
                    svg={footerData.primaryLogoLeft.logo.svg}
                  />
                )}
              </a>
            </Link>
          </div>
          {/* desktop footer only */}
          <div className={styles.columnPrimaryLinks}>
            {footerData.primaryLinks?.map(
              (item: CMSNavGroupLink | CMSLinkProps) => (
                <div className={styles.columnPrimaryLink} key={item._key}>
                  {item._type === 'navGroup' && (
                    <FooterGroup link={item.groupLink} group={item.group} />
                  )}
                  {item._type === 'link' && <FooterItem link={item} />}
                </div>
              ),
            )}
          </div>
        </div>
        <div className={styles.rowSecondary}>
          <div className={styles.columnBottomLeftCopyright}>
            <AdobeLogo className={styles.bottomRightButton} />
            <div className={styles.bottomLeftItem}>
              <span>{footerData.bottomLeftCopyright}</span>
            </div>
            <div className={styles.columnBottomLeftLinks}>
              {footerData.bottomLeftLinks?.map(
                (item: CMSLinkProps | BaseCMSButtonProps) => (
                  <div className={styles.bottomLeftItem} key={item._key}>
                    {item._type === 'link' && (
                      <FooterItem
                        link={item}
                        className={styles.bottomRightLink}
                      />
                    )}
                  </div>
                ),
              )}
            </div>
          </div>

          <div className={styles.columnBottomRightSocials}>
            {footerData.bottomRightSocial?.map((item: LogoLinkProps) => {
              const itemImage = item.svgOrImage.image;
              const imageProps = itemImage
                ? {
                    ...itemImage,
                    sizes: {
                      base: { width: 24 },
                      xs: { width: 24 },
                      sm: { width: 24 },
                      md: { width: 24 },
                      lg: { width: 24 },
                    },
                  }
                : undefined;
              return (
                <Link href={item.url} key={item._key}>
                  <a target="_blank">
                    <SvgOrImage
                      className={styles.socialIcon}
                      svg={item.svgOrImage.svg}
                      image={imageProps}
                      alt={item.label}
                    />
                  </a>
                </Link>
              );
            })}
          </div>
        </div>
      </footer>
    );
  }
  return null;
};

export default Footer;
