import { GetStaticPaths, GetStaticProps } from 'next';
import { useRouter } from 'next/router';

import Image from '~/components/atoms/image';
import GarageDoor from '~/components/molecules/garageDoor';
import ReverseGarageDoor from '~/components/molecules/reverseGarageDoor';
import ModularComponent from '~/components/organisms/ModularComponent';
import PageLayout from '~/components/templates/page-layout';
import { getAllPagesWithSlug } from '~/data';
import { usePreviewSubscription } from '~/data/sanity';
import { ModuleGroup } from '~/types/index.d';
import getPageStaticProps from '~/utils/getPageStaticProps';

import styles from './styles.module.css';
import { DynamicPageProps, StaticPageProps } from './types';

export const getStaticPaths: GetStaticPaths = async () => {
  const allPages = await getAllPagesWithSlug();
  const formattedPages =
    allPages?.map((page) => ({
      params: { slug: page.slug.split('/') },
    })) || [];
  return {
    paths: formattedPages,
    fallback: false,
  };
};

export const getStaticProps: GetStaticProps = async (_props) => {
  const { params, preview = false } = _props;
  let pageSlug = '';
  if (Array.isArray(params?.slug)) {
    pageSlug = params?.slug.join('/') ?? '';
  } else {
    pageSlug = params?.slug ?? '';
  }

  const staticProps = await getPageStaticProps(pageSlug, preview);

  // Escape hatch, if the page is only available in previewMode (on the production branch)
  if (
    staticProps.notFound === true ||
    (process.env.NEXT_PUBLIC_VERCEL_ENV === 'production' &&
      !preview &&
      staticProps.data.previewMode)
  ) {
    return { notFound: true };
  }

  const props: StaticPageProps = {
    preview,
    data: {
      page: staticProps.data,
    },
  };

  if (process.env.NEXT_PUBLIC_VERCEL_ENV !== 'production') {
    props.livePreview = true;
    props.data = {
      ...props.data,
      query: staticProps.query,
      queryParams: staticProps.queryParams,
    };
  }

  return { props };
};

const DynamicPage = ({
  data,
  livePreview,
  globalData,
  preview,
}: DynamicPageProps) => {
  const router = useRouter();
  let page;
  if (livePreview && data?.query) {
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const { data: pageData } = usePreviewSubscription(data?.query, {
      params: data?.queryParams ?? {},
      // The hook will return this on first render
      initialData: data?.page,
      enabled: preview,
    });
    page = pageData;
  } else {
    page = data?.page;
  }

  if (!page) return null;

  return (
    <PageLayout
      globalData={globalData}
      title={page.title}
      description={page.description}
      slug={page.slug}
      ogImage={page.ogImage}
      ogTitle={page.ogTitle}
      disallowRobots={page.disallowRobots}
    >
      {page.moduleGroups.map((group: ModuleGroup) => {
        if ('type' in group && group.type === 'garage') {
          return <GarageDoor group={group} key={group._id} />;
        }
        if ('type' in group && group.type === 'reverseGarage') {
          return <ReverseGarageDoor group={group} key={group._id} />;
        }
        if (group._type !== 'module.spacer') {
          return <ModularComponent key={group._id} {...group} />;
        }
      })}
      {page.footerBackgroundImage && (
        <Image
          key={router.asPath}
          className={styles.footerImage}
          source={page.footerBackgroundImage}
        />
      )}
    </PageLayout>
  );
};

export default DynamicPage;
